import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { Message } from "primeng/primeng";
import { ElementAdjustmentService } from "../../../http/element-adjustment.http.service";
import { ActivatedRoute } from "@angular/router";
import { tableMessage, CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";
import { checkNumber } from "app/@uisftech/common/service/validator";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CreditCardQueryAddBean } from "../bean/credit-card-savings-adjustment-query.bean";

@Component({
    selector: 'credit-approve-more',
    templateUrl: './credit-approve-more.component.html',
    styleUrls: ['./credit-approve-more.component.scss'],
    providers: [Commfunc, ElementAdjustmentService]
})
export class CreditApproveMoreComponent implements OnInit {
    @Input() inValue: any;
    @Output() outDisplay: EventEmitter<any> = new EventEmitter();
    tabMesg: any = tableMessage;//
    msgs: Message[];
    code: any;
    first: number = 0;
    total: string;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    modificationMethodList: any = [];//
    operationTypeOptions: any = [];//
    ch: any = CALENDAR_CN;




    assistantUserIdList: any = [];//
    newAssistantUserIdDot: string;//

    title: string = '';//
    //
    introform: FormGroup;//
    createDate = new Date();//
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    creditCardQueryAddBean: CreditCardQueryAddBean = new CreditCardQueryAddBean();
    updateDisplay: boolean = false;
    constructor(
        public commfunc: Commfunc,
        public httpService: ElementAdjustmentService,
        public routeInfo: ActivatedRoute,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }
    ngOnInit() {
        this.introform = this.fb.group({
            'updateType': new FormControl('', Validators.required),
            'creditCardNo': new FormControl('', Validators.required),
            'assistantUserId': new FormControl('', Validators.required),
            'assistantUserName': new FormControl('', Validators.required),
            'assistantUserIdDot': new FormControl('', Validators.required),
            'newAssistantUserId': new FormControl('', Validators.required),
            'newAssistantUserName': new FormControl('', Validators.required),
            'newAssistantUserIdDot': new FormControl('', Validators.required),
            'userName': new FormControl('', Validators.required),
            'orgName': new FormControl('', Validators.required),
            'createDate': new FormControl('', Validators.required),
        });
        console.log(this.inValue);
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//
        this.operationTypeOptions = this.code['operationTypeList'];//
        this.creditCardQueryAddBean = this.inValue;

        this.newAssistantUserIdDot = this.creditCardQueryAddBean.newAssistantUserIdDot;
        this.assistantUserIdList = [];
        this.assistantUserIdList.push({ label: this.creditCardQueryAddBean.assistantUserId, value: this.creditCardQueryAddBean.assistantUserId })
        this.creditCardQueryAddBean.assistantUserId = this.assistantUserIdList[0].value;
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    custShow() {
        if (this.creditCardQueryAddBean.creditCardNo && this.creditCardQueryAddBean.creditCardNo != '') {
            let param = {
                creditCardNo: this.creditCardQueryAddBean.creditCardNo
            }
            this.httpService.queryCeCardAssistantUserIdInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.assistantUserIdList = [];
                    for (let item of data.assistantUserId) {
                        this.assistantUserIdList.push({ label: item, value: item })
                    }
                    this.creditCardQueryAddBean.assistantUserId = this.assistantUserIdList[0].value;
                    this.creditCardQueryAddBean.assistantUserName = data.assistantUserName;
                    this.creditCardQueryAddBean.assistantUserIdDot = data.assistantUserIdDot;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            });
        }
    }
    //
    showName() {
        if (this.creditCardQueryAddBean.newAssistantUserId && this.creditCardQueryAddBean.newAssistantUserId != '') {
            let param = {
                assistantUserId: this.creditCardQueryAddBean.newAssistantUserId,
            }
            this.httpService.queryUserIdOrAssistantUserId(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.creditCardQueryAddBean.newAssistantUserName = data.userName;
                    this.creditCardQueryAddBean.newAssistantUserIdDot = data.orgId;
                    this.newAssistantUserIdDot = data.orgName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            });
        }
    }
    //
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return
            }
        } else {
            if (num == 1) {
                this.assistantUserIdList = [];
                this.creditCardQueryAddBean.assistantUserId = '';
                this.creditCardQueryAddBean.assistantUserName = '';
                this.creditCardQueryAddBean.assistantUserIdDot = '';
            } else if (num == 2) {
                this.creditCardQueryAddBean.newAssistantUserName = '';
                this.creditCardQueryAddBean.newAssistantUserIdDot = '';
                this.newAssistantUserIdDot = '';
            }
        }
    }
    //
    saveThis() {
        this.creditCardQueryAddBean['adjustType'] = '03';
        this.httpService.updateCeCardAdjustApp(this.creditCardQueryAddBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: '!' });
                this.outDisplay.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
}